// Медіа запити

@mixin mobile {
    @media screen and (min-width: 320px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (min-width: 768px) {
        @content;
    }
}

@mixin desktop {
    @media screen and (min-width: 1280px) {
        @content;
    }
}

@mixin mobile-only {
    @media screen and (max-width: 767px) {
        @content;
    }
}